var _this;
import wInput from '../../components/watch-login/watch-input.vue' //input
import wButton from '../../components/watch-login/watch-button.vue' //button
import md5 from 'js-md5'
export default {
	data() {
		return {
			//logo图片 base64
			logoImage: 'data:image/png;base64,',
			username:'',//用户名
			phone:'', // 用户/电话
			password:'', //密码
			// verCode:"", //验证码
			showAgree:true, //协议是否选择
			isRotate: false, //是否加载旋转
		}
	},
	components:{
		wInput,
		wButton,
	},
	mounted() {
		_this= this;
	},
	methods: {
		isShowAgree(){
			//是否选择协议
			_this.showAgree = !_this.showAgree;
		},
	    startReg() {
			//注册
			if(this.isRotate){
				//判断是否加载中，避免重复点击请求
				return false;
			}
			if (this.showAgree == false) {
			    uni.showToast({
			        icon: 'none',
					position: 'bottom',
			        title: '请先同意《平台协议》'
			    });
			    return false;
			}
			if (this.username.length < 6) {
			    uni.showToast({
			        icon: 'none',
					position: 'bottom',
			        title: '用户名长度不够'
			    });
			    return false;
			}
			
			
	        if (this.password.length < 6) {
	            uni.showToast({
	                icon: 'none',
					position: 'bottom',
	                title: '密码长度不够'
	            });
	            return false;
	        }
			
			if (this.$common.isPhoneNumber(this.phone.length)) {
			    uni.showToast({
			        icon: 'none',
					position: 'bottom',
			        title: '手机号不正确'
			    });
			    return false;
			}
			
			var _data = {
				username: this.username,
				password: md5(md5(this.password) + 'jiehai'),  //加密密码
				phone: this.phone
			}
			_this.isRotate=true
			this.$api.userRegister(_data,res=>{
				_this.isRotate=false
				if(res.code == 0){//注册成功
					uni.showToast({
					    title: '注册成功,等待管理员审核'
					});
					setTimeout(function(){
						uni.navigateTo({
							url: './login'
						})
					},2000)
					
				}else{
					uni.showToast({
					    title: '服务异常'
					});
				}
			});
			
	    }
	}
}
